import React from 'react';
import { BrowserRouter, Route, Switch } from "react-router-dom";


import { Provider } from 'react-redux'

import 'normalize.css';
import './App.scss';
import './Vendor/styles/bootstrap-utilities.min.css';
import store from './Redux/Store/store';
import AtlasPacker from './Views/AtlasPacker/AtlasPacker';
import AnimationTool from './Views/AnimationTool/AnimationTool';
// import HomePage from './Views/HomePage';

const App: React.FC = () => {
  return (
    <Provider store={store}>
      <BrowserRouter>
        <Switch>
          {/* <Route exact path="/" component={AnimationTool} /> */}
          <Route exact path="/tool/animator" component={AnimationTool} />
          {/* <Route exact path="/" component={AtlasPacker} /> */}
          <Route exact path="/tool/atlas-packer" component={AtlasPacker} />
        </Switch>
      </BrowserRouter>
    </Provider>
  );
}

export default App;
