import React, { Component } from 'react';

import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import './ToastNotificationAnimation.scss';
import { ObserverAnim, GLOBAL_EVENT_ANIM } from '../../Observer';

class ToastNotificationAnimation extends Component {

    componentDidMount() {
        ObserverAnim.on(GLOBAL_EVENT_ANIM.NOTIFICATION_SYSTEM.WARNING, (msg: string) => {
            toast.warn(msg, {
                className: 'toast-warn',
                position: "top-center",
                autoClose: 5000,
                hideProgressBar: false,
                closeOnClick: true,
                pauseOnHover: true,
                draggable: true
            });
        });
        ObserverAnim.on(GLOBAL_EVENT_ANIM.NOTIFICATION_SYSTEM.ERROR, (msg: string) => {
            toast.error(msg, {
                className: 'toast-error',
                position: "top-center",
                autoClose: 5000,
                hideProgressBar: false,
                closeOnClick: true,
                pauseOnHover: true,
                draggable: true
            });
        });
        ObserverAnim.on(GLOBAL_EVENT_ANIM.NOTIFICATION_SYSTEM.CLEAR, () => {
            toast.dismiss();
        });
    }

    render() {
        return (
            <ToastContainer />
        )
    }
}

export default ToastNotificationAnimation;