import {SET_ZOOM, ZoomAction, ZoomData, RESET_ZOOM} from '../../Actions/Atlas/zoom.action';
import { RESET_ALL } from '../../Actions/globals.actions';

const defaultZoomData: ZoomData = {
    value: 100
}

export default (state: ZoomData = defaultZoomData, action: ZoomAction ) => {
    switch (action.type) {
        case SET_ZOOM: {
            return action.payload;
        }
        case RESET_ALL: {
            return defaultZoomData
        }
        case RESET_ZOOM: {
            return defaultZoomData;
        }
        default: 
            return state;
    }
}