const clearName = (letters: string) => {
    const chars: any = {
        "": 'a',
        "": 'e',
        "": 'i',
        "": 'o',
        "": 'u',
        "": 'n',
        ' ': '_',
        ',': '',
        ';': '',
        '&': '',
        '/': ''
    };
    return (((letters.toString().toLowerCase()).replace(/[&;,// ]/g, (m: string) =>( chars[m])).split(/(.png|.gif)/)[0])).trim().trimLeft().trimRight().trimEnd();
}

export default clearName;