import React from 'react';
import { useSelector, useDispatch } from 'react-redux';
import { EditingImageName, DeleteImage } from '../../../../Redux/Actions/Atlas/images.action';
import { CloseSideContextMenuAction, ContextMenuData } from '../../../../Redux/Actions/Atlas/contextSideMenu.action';

import './ContextMenu.style.scss';
import { RootState } from '../../../../Redux/Store/store';

let cambio = true;

const ContextMenu = (props: any) => {
    
    const sideMenuContext: ContextMenuData = useSelector((state: RootState) => state.contextSideMenuAtlas);
    const dispatch = useDispatch();
    
    let fadeIn = (cambio) ? 'fadeIn' : 'fadeIn2';
    let contextMenuClassName = `context-menu animated ${(sideMenuContext.active) ? 'active ' + (cambio = !cambio, fadeIn) : ''}`;
    
    // Colocamos la posicin, en el top se corrige 
    const positionXYMenu = {
        top: ((sideMenuContext.y + 80) > globalThis.innerHeight) ? `${sideMenuContext.y - 200}px` : `${sideMenuContext.y}px`,
        left: `${sideMenuContext.x}px`
    };

    const closeMenu = () => {
        dispatch(CloseSideContextMenuAction());
    }

    const handleClickChangeName = () => {
        dispatch(EditingImageName({ id: sideMenuContext.id }));
        closeMenu();
    }

    const handleClickDelete = () => {
        const image = document.querySelector('#id_' + sideMenuContext.id) as HTMLDivElement;
        image.classList.add('zoomOut');
        setTimeout(() => {
            dispatch(DeleteImage({ id: sideMenuContext.id }));
            props.onUpdateSize();
        }, 400);
        closeMenu();
    }

    return (
        <div className={contextMenuClassName} style={positionXYMenu} onContextMenu={(e) => { e.preventDefault() }}>
            <div className="options" onClick={handleClickChangeName}>
                Change Name
            </div>
            <div className="options" onClick={handleClickDelete}>
                Delete image
            </div>
        </div>
    )
}

export default ContextMenu
