/**
 * Nos ayuda a comprobar si es una animacin es correcta
 */
const isPhaserAnimationJSON = (data: string): boolean => {
    let isAtlas = false;
    try {
        JSON.parse(data);
        isAtlas = true;
    } catch (error) {
        isAtlas = false;
    }

    if(isAtlas) {
        const have_anims = (data.search('anims') !== -1);
        const have_frameRate = (data.search('frameRate') !== -1);
        const have_frames = (data.search('frames') !== -1);

        if(!have_frames || !have_frameRate || !have_anims) {
            isAtlas = false;
        } else {
            isAtlas = true;
        }
    }
    return isAtlas;
}

export default isPhaserAnimationJSON;